.datalist{
	position: absolute;
	background-color: #fff;
	-webkit-box-shadow: 0px 0px 2px #d4d4d4;
	        box-shadow: 0px 0px 2px #d4d4d4;
	width: 100%;
}

.datalist ul{
	padding: 4px 10px;
	margin: 0;
}
.datalist li{
	list-style: none;
	padding: 6px 0;
}

.hidden{
	display: none;
}


